.navigation {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.navigation__logo {
  color: #ffffff;
  font-size: 36px;
  letter-spacing: 0.72px;
  line-height: 1;
}

.navigation__number {
  font-size: 26px;
  letter-spacing: 0.78px;
  line-height: 1;
  align-items: center;
  text-align: center;
  display: block;
  height: 100%;
  margin-bottom: 4px;

  &:hover,
  &:focus {
    opacity: 0.7;
  }

  &:active {
    opacity: 0.5;
  }
}

.navigation a {
  display: block;
  font-family: $ff-alumni-sans;
  color: #ffffff;
  font-style: normal;
  font-weight: 600;
  line-height: 1;
  min-width: fit-content;
  margin-top: -5px;

  @include vp-1199 {
    font-size: 24px;
    letter-spacing: 0.48px;
  }

  @include vp-767 {
    font-size: 20px;
    letter-spacing: 0.4px;
  }
}